const arrows = document.querySelector(".nav-arrows");
const woman = document.querySelector(".woman");
const current = document.querySelector(".current");
const circle = document.querySelector(".pink-circle");
let page = 1;
arrows.addEventListener("click", function (e) {
	const target = e.target;

	if (target.classList.contains("next") && page !== 2) {
		woman.style.transform = `translateX(-50%) translateY(100rem)`;
		page = 2;
		circle.classList.toggle("active-circle");
		current.textContent = `0${page}`;
		setTimeout(() => {
			woman.src = `./dist/images/woman1.png`;
			woman.style.transform = `translateX(-50%) translateY(-5rem)`;
			circle.classList.toggle("active-circle");
		}, 400);
	} else if (target.classList.contains("prev") && page !== 1) {
		woman.style.transform = `translateX(-50%) translateY(100rem)`;
		page = 1;
		circle.classList.toggle("active-circle");

		current.textContent = `0${page}`;
		setTimeout(() => {
			woman.src = `./dist/images/woman2.png`;
			woman.style.transform = `translateX(-50%) translateY(-5rem)`;
			circle.classList.toggle("active-circle");
		}, 400);
	}
});
